	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝、鄧小平は1976年に「Gang of Four」が1976年に何をしたのかを描き、党内の激しい政治闘争を描いた。内部資料の多くは、それを書いた奇妙な、多くの内部情報、元の大きなセクションを持つことができますがあります。権威ある出版物がないため、歴史的な事実の総数を判断することはできません。しかし、いくつかの点では、それは特定の真正性を持っています。</p>\n<p>授業では、副大統領がそこで待っています。これは、基本的なコースの学校システム、博士課長、紹介する学校のリーダーです。学校は人を育てる場所です。最高の先生は、学生が知識と真実を追求するのを本当に刺激できるように、最初のレベルで学生を分類します。フーリエ大学は学期に従事しており、大丈夫だと言われています。私は、学生が私たちの最も重要な製品であると言いました。私たちの部門の目標は、この部門が社会での地位と評判を得ることができるように、最高の学生を育成することです。</p>\n<p>立法府が開催した上海政府の法律業務について議論するセミナーには、市の第6回会議室に出席する。誰もが多くの意見を言った。副市長は、社会主義市場経済システムの法制度の確立、知的財産権の保護、法執行の強化、法的宣伝の強化、法律の改善などに焦点を当て、法制度の構築を強化する必要があると述べた。法制度構築は経済発展の重要な部分であり、我々はそれを解決するために大きな努力をしなければならない。同時に、私たちは積極的に専門家の役割を果たし、市の法制度構築に参加し、法制度の設計に参加しなければなりません。私たちは法制度が脆弱な国では法制度構築に取り組んでいます。農民を育てる農民のように、まずルールを死に至らせることはできません。バスケットボールは戦いができず、最初は歩くことしかできません。バスケットボールは、遊ぶことができ、ゆっくりと締め付けられます。しかし、ボクシング競技のようないくつかの基本的なルールが確立されていなければなりません。最初は、腰の下部を使うことができないと規定する必要があります。笑う。</p>\n<p>金属取引所の関係者は、新しい税制の後、上海は付加価値税（VAT）を受け取って現場で受け取らなかったことを報告した。上海の取引量は10億元から1億元に大幅に減少した。外国為替には多くのビジネスが行きました。これは今日の中国社会の発展の問題です。地方自治体は独立して不均衡があります。バランスの取れた国家法執行を維持するために、中央巡回裁判所を設置すべきである。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0041.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0041.md","frontmatter":{"title":"2月24日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0041.md absPath of file >>> MarkdownRemark"}}

